.home {
  max-width: 100%;
}

.hero {
  text-align: center;
  padding: 3rem 0;
  background: linear-gradient(135deg, #3498db, #2c3e50);
  color: white;
  margin: -2rem -1rem 2rem -1rem;
  border-radius: 0 0 20px 20px;
}

.hero h1 {
  font-size: 3rem;
  margin: 0 0 1rem 0;
  font-weight: bold;
}

.hero-subtitle {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
  opacity: 0.9;
}

.services {
  margin: 3rem 0;
}

.services h2 {
  text-align: center;
  font-size: 2.2rem;
  margin-bottom: 2rem;
  color: #2c3e50;
}

.service-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.service-card {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  text-decoration: none;
  color: inherit;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid #e0e0e0;
}

.service-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.service-icon {
  font-size: 3rem;
  text-align: center;
  margin-bottom: 1rem;
}

.service-card h3 {
  font-size: 1.4rem;
  margin: 0 0 1rem 0;
  color: #2c3e50;
  text-align: center;
}

.service-card p {
  color: #666;
  line-height: 1.6;
  text-align: center;
  margin: 0;
}

.features {
  margin: 4rem 0;
  padding: 3rem 0;
  background-color: #f8f9fa;
  border-radius: 12px;
}

.features h2 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  color: #2c3e50;
}

.feature-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  padding: 0 2rem;
}

.feature-item {
  text-align: center;
}

.feature-item h4 {
  font-size: 1.2rem;
  margin: 0 0 0.5rem 0;
  color: #2c3e50;
}

.feature-item p {
  color: #666;
  margin: 0;
  line-height: 1.5;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .hero {
    padding: 2rem 1rem;
    margin: -1rem -1rem 2rem -1rem;
  }
  
  .hero h1 {
    font-size: 2.2rem;
  }
  
  .hero-subtitle {
    font-size: 1rem;
  }
  
  .service-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .service-card {
    padding: 1.5rem;
  }
  
  .features {
    padding: 2rem 0;
  }
  
  .feature-list {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    padding: 0 1rem;
  }
}