.cultural-guide {
  max-width: 900px;
  margin: 0 auto;
}

.page-header {
  text-align: center;
  margin-bottom: 2rem;
}

.page-header h1 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin: 0 0 0.5rem 0;
}

.page-header p {
  font-size: 1.1rem;
  color: #666;
  margin: 0;
}

.cultural-content {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.search-sections {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 2rem;
}

.search-section {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 1.5rem;
  border: 1px solid #e9ecef;
}

.search-section h3 {
  color: #2c3e50;
  margin: 0 0 1rem 0;
  font-size: 1.2rem;
}

.topic-search .form-group {
  margin-bottom: 1rem;
}

.topic-search button {
  width: 100%;
}

.location-search {
  text-align: center;
}

.location-search p {
  margin-bottom: 1rem;
  color: #666;
  font-size: 0.9rem;
}

.location-search button {
  width: 100%;
}

.festival-search .form-group {
  margin-bottom: 1rem;
}

.festival-search button {
  width: 100%;
}

.results-section {
  margin-top: 2rem;
}

.results-section h3 {
  color: #2c3e50;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

.cultural-card {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 2rem;
  border-left: 4px solid #3498db;
}

.cultural-card h4 {
  color: #2c3e50;
  margin: 0 0 1rem 0;
  font-size: 1.3rem;
}

.cultural-description {
  margin-bottom: 1.5rem;
}

.cultural-description p {
  color: #666;
  line-height: 1.6;
  margin: 0;
}

.practical-tips {
  margin-bottom: 1.5rem;
}

.practical-tips h5 {
  color: #2c3e50;
  margin: 0 0 0.75rem 0;
  font-size: 1.1rem;
}

.practical-tips ul {
  margin: 0;
  padding-left: 1.5rem;
}

.practical-tips li {
  margin-bottom: 0.5rem;
  color: #666;
  line-height: 1.5;
}

.do-donts {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-bottom: 1.5rem;
}

.dos, .donts {
  background: white;
  border-radius: 8px;
  padding: 1rem;
}

.dos h5, .donts h5 {
  margin: 0 0 0.75rem 0;
  font-size: 1rem;
}

.dos h5 {
  color: #28a745;
}

.donts h5 {
  color: #dc3545;
}

.dos ul, .donts ul {
  margin: 0;
  padding-left: 1.5rem;
}

.dos li, .donts li {
  margin-bottom: 0.5rem;
  color: #666;
  line-height: 1.4;
  font-size: 0.9rem;
}

.relevant-locations h5 {
  color: #2c3e50;
  margin: 0 0 1rem 0;
  font-size: 1.1rem;
}

.locations-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.location-item {
  background: white;
  border-radius: 6px;
  padding: 1rem;
  border: 1px solid #e9ecef;
}

.location-item strong {
  color: #2c3e50;
  display: block;
  margin-bottom: 0.25rem;
}

.location-item p {
  margin: 0.25rem 0;
  color: #666;
  font-size: 0.9rem;
}

.location-item small {
  color: #999;
  font-size: 0.8rem;
}

.festivals-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 1.5rem;
}

.festival-card {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 1.5rem;
  border: 1px solid #e9ecef;
}

.festival-card h4 {
  color: #2c3e50;
  margin: 0 0 0.5rem 0;
  font-size: 1.2rem;
}

.festival-date {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.festival-description {
  margin-bottom: 1rem;
}

.festival-description p {
  color: #666;
  line-height: 1.5;
  margin: 0;
}

.cultural-significance {
  margin-bottom: 1rem;
}

.cultural-significance h5 {
  color: #2c3e50;
  margin: 0 0 0.5rem 0;
  font-size: 1rem;
}

.cultural-significance p {
  color: #666;
  line-height: 1.5;
  margin: 0;
  font-size: 0.9rem;
}

.festival-locations h5 {
  color: #2c3e50;
  margin: 0 0 0.5rem 0;
  font-size: 1rem;
}

.festival-locations ul {
  margin: 0;
  padding-left: 1.5rem;
}

.festival-locations li {
  margin-bottom: 0.25rem;
  color: #666;
  font-size: 0.9rem;
}

.no-festivals {
  text-align: center;
  padding: 2rem;
  color: #666;
  background: #f8f9fa;
  border-radius: 8px;
  margin-top: 1rem;
}

.empty-state {
  text-align: center;
  padding: 3rem 1rem;
  color: #666;
  font-size: 1.1rem;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .cultural-content {
    padding: 1.5rem;
  }
  
  .search-sections {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .do-donts {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .locations-grid {
    grid-template-columns: 1fr;
  }
  
  .festivals-grid {
    grid-template-columns: 1fr;
  }
}