.traffic-estimator {
  max-width: 900px;
  margin: 0 auto;
}

.page-header {
  text-align: center;
  margin-bottom: 2rem;
}

.page-header h1 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin: 0 0 0.5rem 0;
}

.page-header p {
  font-size: 1.1rem;
  color: #666;
  margin: 0;
}

.traffic-content {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.route-form {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #e9ecef;
}

.route-form h3 {
  color: #2c3e50;
  margin-bottom: 1.5rem;
}

.form-actions {
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;
}

.popular-routes {
  margin-bottom: 2rem;
}

.popular-routes h4 {
  color: #2c3e50;
  margin-bottom: 1rem;
}

.route-suggestions {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.route-suggestion {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 20px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.9rem;
}

.route-suggestion:hover {
  background: #e9ecef;
  border-color: #3498db;
}

.results-section {
  margin-top: 2rem;
}

.results-section h3 {
  color: #2c3e50;
  margin-bottom: 1.5rem;
}

.main-route {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 1.5rem;
  border-left: 4px solid #3498db;
}

.route-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.route-header h4 {
  margin: 0;
  color: #2c3e50;
  font-size: 1.2rem;
}

.traffic-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.traffic-icon {
  font-size: 1.2rem;
}

.traffic-level {
  font-weight: 600;
  text-transform: capitalize;
}

.route-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 1rem;
}

.detail-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background: white;
  border-radius: 6px;
}

.detail-label {
  color: #666;
  font-size: 0.9rem;
}

.detail-value {
  font-weight: 600;
  color: #2c3e50;
}

.alternatives-preview {
  text-align: center;
  padding-top: 1rem;
  border-top: 1px solid #e9ecef;
}

.alternatives-preview p {
  margin: 0 0 1rem 0;
  color: #666;
}

.alternatives-section {
  margin-top: 2rem;
}

.alternatives-section h3 {
  color: #2c3e50;
  margin-bottom: 1.5rem;
}

.alternatives-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
}

.alternative-route {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  padding: 1rem;
}

.alternative-route .route-header h4 {
  font-size: 1rem;
}

.alternative-route .route-details {
  grid-template-columns: 1fr;
  gap: 0.5rem;
  margin-bottom: 0;
}

.alternative-route .detail-item {
  padding: 0.25rem 0.5rem;
}

.empty-state {
  text-align: center;
  padding: 3rem 1rem;
  color: #666;
  font-size: 1.1rem;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .traffic-content {
    padding: 1.5rem;
  }
  
  .route-suggestions {
    flex-direction: column;
  }
  
  .route-header {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }
  
  .traffic-indicator {
    align-self: flex-end;
  }
  
  .route-details {
    grid-template-columns: 1fr;
  }
  
  .alternatives-grid {
    grid-template-columns: 1fr;
  }
}