.slang-translator {
  max-width: 800px;
  margin: 0 auto;
}

.page-header {
  text-align: center;
  margin-bottom: 2rem;
}

.page-header h1 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin: 0 0 0.5rem 0;
}

.page-header p {
  font-size: 1.1rem;
  color: #666;
  margin: 0;
}

.translator-content {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.translator-form {
  margin-bottom: 2rem;
}

.input-container {
  position: relative;
}

.clear-button {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #999;
  cursor: pointer;
  padding: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.clear-button:hover {
  color: #666;
}

.suggestions {
  margin-top: 0.5rem;
}

.suggestions-label {
  font-size: 0.9rem;
  color: #666;
  margin: 0 0 0.5rem 0;
}

.suggestions-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.suggestion-item {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 20px;
  padding: 0.25rem 0.75rem;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.suggestion-item:hover {
  background: #e9ecef;
  border-color: #3498db;
}

.form-actions {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.translation-result {
  margin: 2rem 0;
}

.translation-result h3 {
  color: #2c3e50;
  margin-bottom: 1rem;
}

.result-card {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 1.5rem;
  border-left: 4px solid #3498db;
}

.result-card > div {
  margin-bottom: 0.75rem;
}

.result-card > div:last-child {
  margin-bottom: 0;
}

.original-phrase {
  font-size: 1.1rem;
}

.translated-phrase {
  font-size: 1.2rem;
  color: #2c3e50;
}

.confidence {
  font-size: 0.9rem;
  color: #666;
}

.alternatives ul {
  margin: 0.5rem 0 0 1rem;
  padding: 0;
}

.alternatives li {
  margin-bottom: 0.25rem;
}

.help-section {
  margin-top: 3rem;
  padding-top: 2rem;
  border-top: 1px solid #e9ecef;
}

.help-section h3 {
  color: #2c3e50;
  margin-bottom: 1rem;
}

.popular-terms {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.term-button {
  background: #3498db;
  color: white;
  border: none;
  border-radius: 20px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
  font-size: 0.9rem;
}

.term-button:hover {
  background: #2980b9;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .translator-content {
    padding: 1.5rem;
  }
  
  .popular-terms {
    justify-content: center;
  }
  
  .suggestions-list {
    justify-content: center;
  }
}