.food-recommendations {
  max-width: 1000px;
  margin: 0 auto;
}

.page-header {
  text-align: center;
  margin-bottom: 2rem;
}

.page-header h1 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin: 0 0 0.5rem 0;
}

.page-header p {
  font-size: 1.1rem;
  color: #666;
  margin: 0;
}

.food-content {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.preferences-section {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #e9ecef;
}

.preferences-section h3 {
  color: #2c3e50;
  margin-bottom: 1.5rem;
}

.preference-group {
  margin-bottom: 1.5rem;
}

.dietary-options {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-top: 0.5rem;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  font-size: 0.9rem;
}

.checkbox-label input[type="checkbox"] {
  margin: 0;
}

.checkbox-text {
  text-transform: capitalize;
}

.budget-inputs {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 0.5rem;
}

.budget-input {
  width: 120px;
}

.action-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

.location-notice {
  margin-top: 0.5rem;
  color: #666;
  font-style: italic;
}

.results-section {
  margin-top: 2rem;
}

.results-section h3 {
  color: #2c3e50;
  margin-bottom: 1.5rem;
}

.food-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1.5rem;
}

.food-card {
  border: 1px solid #e9ecef;
  border-radius: 12px;
  padding: 1.5rem;
  background: #f8f9fa;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.food-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

.food-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.food-header h4 {
  margin: 0;
  color: #2c3e50;
  font-size: 1.2rem;
}

.safety-rating {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  font-size: 0.9rem;
}

.rating-stars {
  color: #ffc107;
  font-size: 1rem;
}

.rating-text {
  color: #666;
  font-size: 0.8rem;
}

.food-details > div {
  margin-bottom: 1rem;
}

.food-details > div:last-child {
  margin-bottom: 0;
}

.location-info strong {
  color: #2c3e50;
  display: block;
  margin-bottom: 0.25rem;
}

.location-info p {
  margin: 0.25rem 0;
  color: #666;
  font-size: 0.9rem;
}

.location-info small {
  color: #999;
  font-size: 0.8rem;
}

.price-hours {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background: white;
  border-radius: 6px;
  font-size: 0.9rem;
}

.price {
  font-weight: 600;
  color: #28a745;
}

.hours {
  color: #666;
}

.popular-items strong {
  color: #2c3e50;
  display: block;
  margin-bottom: 0.25rem;
}

.popular-items p {
  margin: 0;
  color: #666;
  font-size: 0.9rem;
}

.dietary-badges {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.dietary-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  color: white;
  font-size: 0.75rem;
  font-weight: 500;
  text-transform: capitalize;
}

.empty-state {
  text-align: center;
  padding: 3rem 1rem;
  color: #666;
  font-size: 1.1rem;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .food-content {
    padding: 1.5rem;
  }
  
  .dietary-options {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .budget-inputs {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .budget-input {
    width: 100%;
  }
  
  .action-buttons {
    flex-direction: column;
  }
  
  .food-grid {
    grid-template-columns: 1fr;
  }
  
  .food-header {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .safety-rating {
    align-items: flex-start;
  }
  
  .price-hours {
    flex-direction: column;
    gap: 0.25rem;
    text-align: center;
  }
}